.navbar-nav .nav-item .sub-menu::after {
  content: "";
  position: absolute;
  background: #fff;
  width: 20px;
  height: 20px;
  top: -10px;
  left: 20%;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
