  overflow-x: hidden;
}

/* --Media Query for the sidetas/sidenav--- */

@media screen and (max-width: 996px) {
  .sidenav,
  .sidenav.active {
    width: 40%;
    top: 5.6em;
  }
}

@media screen and (max-width: 770px) {
  .sidenav,
  .sidenav.active {
    width: 50%;
    top: 3em;
  }
}

@media screen and (max-width: 550px) {
  .sidenav,
  .sidenav.active {
    width: 100%;
    top: 3em;
  }
}

